import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';
import { SKINS } from '../../constant/props';

import paths from './paths';

class Icon extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			name,
			skin,
			className,
			...others
		} = props;

		const klassName = KlassName('icon',{
			['icon--' + skin]:skin,
		},className);

		let path = paths[name] || false;

		return (
			<svg
				{...others}
				name={name}
				className={klassName}
				viewBox="0 0 32 32"
			>
				<path d={path} />
			</svg>
		);
	}
};

Icon.propTypes = {
	skin: PropTypes.oneOf(SKINS),

	name: PropTypes.string,
	className: PropTypes.string,
	width: PropTypes.string,
	height: PropTypes.string,
	fill: PropTypes.string,
};

Icon.defaultProps = {
	name: 'home',
	width: '.875em',
	height: '.875em',
	fill: 'currentColor',
};

export default Icon;